import { openDB, deleteDB, wrap, unwrap } from 'idb';  // https://github.com/jakearchibald/idb
import { mockHyperlinks } from "./DataMock";

const DATABASE_NAME = 'savelinksforlater';  // when user is not logged in then this is what could be used or, we could just not use persistent storage at all and skip this entirely.
// const DATABASE_NAME = 'savelinksforlater - user@email.com - guid-for-security';
const DATABASE_VERSION = 2;
let _db;

class DataLocal {

	constructor() {
		this.initIndexedDb();
	}

	async getHyperlinks() {
		return await _db.getAllFromIndex('hyperlinks', 'createdOn');
	}

	async addOrUpdateHyperlink(hyperlink) {
		await _db.put('hyperlinks', hyperlink);
	}

	async deleteHyperlink(id) {
		await _db.delete('hyperlinks', id);
	}

	async initIndexedDb(doInitMockHyperlinks = false) {
		const db = await openDB(DATABASE_NAME, DATABASE_VERSION, {
			upgrade(upgradeDb, oldVersion, newVersion, transaction) {
				/* tslint:disable */
				switch (oldVersion) {
					case 0:
						// a placeholder case so that the switch block will
						// execute when the database is first created
						// (oldVersion is 0)
					case 1:
						// init version 1 of hyperlinks store
						const store = upgradeDb.createObjectStore('hyperlinks', {
							keyPath: 'id',
							autoIncrement: false,
						});
						store.createIndex('id', 'id');
						store.createIndex('title', 'title');
						store.createIndex('url', 'url');
						store.createIndex('visited', 'visited');
						store.createIndex('createdOn', 'createdOn');
				}
			},
			blocked() {
			// …
			},
			blocking() {
			// …
			},
			terminated() {
			// …
			},
		});

		if(doInitMockHyperlinks)
			await this.initMockHyperlinks(db);
		
		_db = db;
	}

	async deleteIndexedDb() {
		await deleteDB(DATABASE_NAME, {
			blocked() {
			  	// …
			},
		});
	}
	
	async initMockHyperlinks(db) {
		// Add multiple hyperlinks in one transaction:
		{
			const tx = db.transaction('hyperlinks', 'readwrite');
			for (const mockHyperlinkId in mockHyperlinks) {
				if(!mockHyperlinks.hasOwnProperty(mockHyperlinkId))
					return;
				const mockHyperlink = mockHyperlinks[mockHyperlinkId]
				tx.store.put('hyperlinks', mockHyperlink)   // add or update
			}
			await tx.done;
		}
	}
}

export default new DataLocal();